footer {
    grid-row-start: 2;
    grid-row-end: 3;
}
.footer {
    background-color: rgb(0, 0, 0);
    padding: 15px 0 100px;
    text-align: center;
    position: relative;
    &__logo {
        margin-bottom: 50px;
        &::before {
            position: absolute;
            transform: translate(-50%);
            // filter: invert(100%) sepia(0) saturate(0%) hue-rotate(0) brightness(1180) contrast(119%);
            content: url("../../img/header/logo-min.png");
        }
    }
    &__list {
        margin: 0 auto;
        max-width: 900px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 50px;
        font-size: 21px;
        font-family: "Overpass";
        &-link {
            color: rgb(255, 255, 255);
        }
    }
}
.icon {
    margin: 0 auto;
    width: 270px;
    color: rgb(255, 255, 255);
    cursor: pointer;
    filter: invert(100%) sepia(0) saturate(0%) hue-rotate(0) brightness(1180) contrast(119%);
}
